import React from 'react'


const Nav = ({onRouteChange, isSignedIn}) => {
  if (isSignedIn) {
    return (
        <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
          <p onClick={() => onRouteChange('signout')} className='f4 link dim black underline pa3 pointer'>SignOut</p>
        </nav>
    );
  }
  else {
    return (
        <div>
          <nav style={{display: 'flex', justifyContent: 'flex-end'}}>
            <p onClick={() => onRouteChange('signin')} className='f4 link dim black underline pa3 pointer'>Sign In</p>
            <p onClick={() => onRouteChange('register')} className='f4 link dim black underline pa3 pointer'>Register</p>
          </nav>
        </div>
    );
  }
}

export default Nav